import React from 'react';

jest.useFakeTimers();
jest.mock('axios');

import {create} from 'react-test-renderer';
import {PlayersProvider} from '../src/context/Players';
import Players from '../src/screens/Players';
import {usePlayers} from '../src/context/Players';

const tree = create(
    <PlayersProvider>
        <Players />
    </PlayersProvider>
);

const {getPlayers} = usePlayers();

describe('Players', () => {
    test('renders correctly', () => {
        tree = create(<Players />).toJSON();
        expect(tree).toMatchSnapshot();
    });

    test('get players correctly', async () => {
        const players = await getPlayers();
        expect(players.data.response).toEqual([]);
    });
});
